module.exports = ".nepali-date-picker {\n    background: #ffffff none repeat scroll 0 0;\n    border : 1px solid #cccccc;\n    box-shadow: 0 5px 15px -5px rgba(0, 0, 0, 0.506);\n    box-sizing: border-box;\n    color: #000000;\n    display: block;\n    font-family: \"Helvetica Neue\",\"Helvetica\",\"Arial\",sans-serif;\n    padding: 8px;\n    position: absolute;\n    z-index: 9999;\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    width: 270px;\n    height: 220px;\n}\n\n.nepali-date-picker table{\n    background-color: transparent;\n    border-collapse: collapse;\n    width: 100%;\n    max-width: 100%;\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    border: 2px solid rgba(129, 120, 120, 0.17);\n}\n\n.nepali-date-picker table thead{\n    background: #dedede;\n}\n\n.nepali-date-picker table th {\n    color: #999;\n    cursor: default;\n    font-weight: 700;\n    text-align: center;\n    cursor: default;\n}\n\n.nepali-date-picker table th {\n    background: #f1f1f1 none repeat scroll 0 0;\n}\n\n.nepali-date-picker table thead td {\n    color: #8b8686;\n    font-weight: bold;\n}\n\n.nepali-date-picker table td {\n    border: 1px solid rgba(154, 150, 150, 0.46);\n    color: #666666;\n    padding: 0;\n    width: 35px;\n    text-align: center;\n    font-size: 14px;\n}\n\n.nepali-date-picker table tbody td{\n    cursor: pointer;\n}\n\n.nepali-date-picker table tbody td.current-month-date:hover{\n    background-color: #718fcd;\n    color: white;\n    font-weight: bold;\n}\n\n.nepali-date-picker table td.other-month-date, .nepali-date-picker table td.current-month-date.disable {\n    color: #cccccc;\n    cursor: default;\n}\n\n.nepali-date-picker table tbody td.current-month-date.disable:hover{\n    background-color: inherit;\n    color: #cccccc;\n    font-weight: normal;\n}\n\n.nepali-date-picker .icon {\n    opacity: 0.5;\n    background-image: url(\"data:image/png;base64,\");\n}\n\n.nepali-date-picker .icon:hover{\n    opacity: 1;\n}\n\n.nepali-date-picker .prev-btn.icon {\n    background-position: 80px center;\n    float: left;\n    height: 30px;\n    width: 20px;\n}\n\n.current-month-date.active, .drop-down-content li.active {\n    background-color: #7bde77;\n    color: white;\n    font-weight: bold;\n}\n\n.nepali-date-picker .next-btn.icon {\n    background-position: 0 center;\n    float: right;\n    height: 30px;\n    width: 20px;\n}\n\n.nepali-date-picker .today-btn.icon {\n    background-position: 130px center;\n    display: block;\n    float: left;\n    height: 30px;\n    margin: 0 15px;\n    width: 20px;\n}\n\n.nepali-date-picker .current-month-txt {\n    color: #545b54;\n    cursor: pointer;\n    display: inline-block;\n    font-weight: bold;\n    line-height: 30px;\n    padding-right: 20px;\n    position: relative;\n    text-align: right;\n    width: 80px;\n}\n\n.nepali-date-picker .current-year-txt {\n    color: #545b54;\n    font-weight: bold;\n    /* padding-right: 20px; */\n    cursor: pointer;\n    position: relative;\n    display: inline-block;\n    line-height: 30px;\n    width: 80px;\n    text-align: left;\n}\n\n.nepali-date-picker .current-year-txt:hover, .nepali-date-picker .current-month-txt:hover{\n    text-decoration: underline;\n}\n\n.nepali-date-picker .calendar-controller i.icon-drop-down {\n    background-position: 12px -15px;\n    height: 30px;\n    position: absolute;\n    width: 20px;\n}\n\n.nepali-date-picker .drop-down-content {\n    background-color: #ffffff;\n    border: 1px solid #cccccc;\n    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.32);\n    display: none;\n    height: 99px;\n    padding: 5px;\n    position: absolute;\n    width: 100%;\n    /*overflow: hidden;*/\n}\n\n.nepali-date-picker .scrollbar-wrapper {\n    border-left: 1px solid rgba(204, 204, 204, 0.2);\n    height: 100%;\n    position: absolute;\n    right: 0;\n    top: 0;\n    width: 10px;\n}\n\n.scrollbar {\n    background-color: black;\n    border-radius: 2.5px;\n    display: block;\n    height: 100%;\n    opacity: 0.5;\n    position: absolute;\n    right: 6.5px;\n    /* width: 5px; */\n    width: 100%\n}\n\n.nepali-date-picker .drop-down-content .option-wrapper {\n    height: 100%;\n    overflow-x: hidden;\n    overflow-y: scroll;\n    padding: 0;\n    position: relative;\n}\n\n.nepali-date-picker .drop-down-content ul {\n    list-style: outside none none;\n    margin: 0;\n    padding: 0 5px 0 0;\n}\n\n.drop-down-content li {\n    border-bottom: 1px solid rgba(159, 153, 153, 0.39);\n    font-size: 11px;\n    font-weight: normal;\n    line-height: 16px;\n    text-align: right;\n}\n\n.drop-down-content li:hover{\n    background: #718fcd;\n}\n\n.drop-down-content li:last-child {\n    border-bottom: medium none;\n}\n\n::-webkit-scrollbar {\n    width: 13px;\n    height: 13px;\n}\n\n::-webkit-scrollbar-track\n{\n    background: rgba(0, 0, 0, 0.1);\n}\n\n::-webkit-scrollbar-thumb\n{\n    background: rgba(0, 0, 0, 0.5);\n}\n"